@use "base/variables" as v;
@use "base/mixins" as m;

.main-nav {
    .navbar-brand {
        .logo-main {
            width: 20rem !important;
        }
    }
}

.single-blog {
    h3 {
        text-align: center;
        margin-top: 5rem;
    }

    p {
        margin-bottom: 2.5rem;
    }
}

.blog-single {
    padding-top: 0;
    .blog-single-presentation {
        margin-top: 5rem;
        ul {
            width: 95%;
        }
    }
}

@include m.tablet {
    .blog-single .single-blog img {
        width: 100%;
        margin-left: 0;
        margin-right: 0;
    }
}