html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(1.15rem + 0.3vw);
}

p {
  font-size: calc(1.5rem + 0.5vw);
}

a {
  text-decoration: none;
  color: #212121;
}

button {
  background-color: transparent;
  padding: 0.5rem 1rem;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

footer {
  margin-top: auto;
  text-align: center;
  padding: 1rem auto;
}

.main-nav .navbar-brand .logo-main {
  width: 20rem !important;
}

.single-blog h3 {
  text-align: center;
  margin-top: 5rem;
}
.single-blog p {
  margin-bottom: 2.5rem;
}

.blog-single {
  padding-top: 0;
}
.blog-single .blog-single-presentation {
  margin-top: 5rem;
}
.blog-single .blog-single-presentation ul {
  width: 95%;
}

@media (min-width: 768px) {
  .blog-single .single-blog img {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}
blockquote {
  margin-top: 5vw;
}

h1,
h2,
h3 {
  font-family: "Klein Condensed Trial", sans-serif;
  font-size: calc(2rem + 0.5vw) !important;
  color: #2a2faf;
}

p,
li {
  color: gray;
  font-family: "Times New Roman", Times, serif;
  font-size: calc(1.25rem + 0.25vw) !important;
}

th {
  font-family: "Open Sauce One", sans-serif;
  font-size: calc(1rem + 0.25vw) !important;
}

td {
  font-family: "Times New Roman", Times, serif;
  font-size: calc(1.25rem + 0.25vw) !important;
  color: gray !important;
}

.red-rectangle {
  padding: 0.5rem;
  width: 100%;
  height: 1rem;
  background-color: red;
  margin-bottom: 5rem;
}

.blog-single .single-blog blockquote::before {
  background-color: red !important;
}

#table-scroll {
  max-height: 500px;
  overflow: scroll;
  width: 95%;
  flex-shrink: 0;
  overflow: auto;
  position: relative;
  width: 95%;
  z-index: 1;
  margin: auto;
}
#table-scroll > * {
  width: 83.33%;
  margin: auto;
}
#table-scroll table {
  margin-bottom: 5rem;
  margin-left: 0;
  width: 95%;
  border-collapse: separate;
  border-spacing: 0;
  margin: 0;
}
#table-scroll table tbody > tr > td:first-child {
  background-color: #f2f2f2;
}
#table-scroll table tbody > tr > td:first-child:nth-child(3) {
  background-color: red;
}
#table-scroll table tbody > tr > td:nth-child(2) {
  background-color: #f2f2f2;
  position: sticky;
  left: 0;
}
#table-scroll table tbody > tr:first-child {
  background-color: #f2f2f2;
  position: sticky;
  top: 0;
  z-index: 3;
}
#table-scroll table tr td {
  padding: 0.5rem 2rem;
  white-space: nowrap;
  width: 1rem !important;
}
#table-scroll h3 {
  text-align: center;
  margin-bottom: 5rem;
}
#table-scroll tr:nth-child(even) {
  background-color: #f2f2f2;
}

.panel {
  width: 95%;
  margin: auto;
  padding: 2rem;
  padding-left: 0;
  color: gray;
  font-family: "Times New Roman", Times, serif;
  font-size: calc(1.25rem + 0.25vw) !important;
}
.panel label {
  padding-right: 1rem;
}
.panel input {
  border: 1px solid #f2f2f2;
  background-color: #f2f2f2;
  padding-left: 1rem;
}

header {
  display: flex;
  justify-content: center;
}
header img {
  width: calc(5rem + 25vw);
  margin: auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.blog .row-article .blog-post-titie a {
  font-family: "Open Sauce One", sans-serif;
  font-size: calc(1rem + 0.25vw) !important;
}
.blog .row-article .blog-post-action {
  color: #2a2faf;
  font-weight: bold;
}
@media (min-width: 768px) {
  .blog .row-article {
    justify-content: center;
  }
}

.column-news {
  display: flex;
  width: 100%;
  flex-direction: column;
  margin: auto;
}
@media (min-width: 992px) {
  .column-news {
    width: 83.33%;
    flex-direction: row;
  }
  .column-news > * {
    width: 49.5%;
  }
}
.column-news .news {
  background-color: #f2f2f2;
  padding: 2rem;
  border-radius: 1rem;
  margin-top: 5rem;
  margin-bottom: 2.5rem;
}
.column-news .news h3 {
  text-align: center;
}
.column-news .news .new-item {
  margin: 2rem;
  display: flex;
}
.column-news .news .new-item .new-img {
  width: 50%;
  height: 25vw;
}
@media (min-width: 992px) {
  .column-news .news .new-item .new-img {
    height: 10vw;
  }
}
.column-news .news .new-item .new-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.column-news .news .new-item .new-info {
  width: 40%;
  padding-left: 2rem;
}
.column-news .news .new-item .new-info a {
  transition: all 0.5s;
}
.column-news .news .new-item .new-info a:hover {
  color: #2a2faf;
  transition: all 0.5s;
}

.blog-single blockquote {
  margin-bottom: 0 !important;
}
.blog-single p {
  text-align: justify;
}
.blog-single .column-blog {
  justify-content: center;
}
.blog-single .column-blog .single-blog {
  padding: 0;
}
.blog-single .column-blog .single-blog p span {
  font-weight: bold;
  color: #002338;
}
.blog-single .two-columns {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .blog-single .two-columns {
    flex-direction: row;
    justify-content: space-between;
  }
  .blog-single .two-columns > * {
    width: 48%;
  }
}

.faq-container {
  width: 90%;
}
.faq-container .accordion-item {
  border: none;
  margin-bottom: 2rem;
}
.faq-container h3 {
  text-align: center;
  margin-bottom: 5rem;
}
.faq-container .accordion-header button {
  font-family: "Open Sauce One", sans-serif;
  font-size: calc(1.25rem + 0.25vw) !important;
  font-weight: bold;
}
.faq-container .accordion-body {
  font-family: "Times New Roman", Times, serif;
  font-size: calc(1.25rem + 0.25vw) !important;
  color: gray;
}

.accordion-button:hover,
.accordion-button:focus,
.accordion-button:not(.collapsed) {
  background-color: transparent !important;
  color: inherit !important;
  box-shadow: none !important;
}