@use "base/variables" as v;
@use "base/mixins" as m;

blockquote {
    margin-top: 5vw;
}

h1,
h2,
h3 {
    font-family: 'Klein Condensed Trial', sans-serif;
    // font-family: 'Klein Trial', sans-serif;
    // font-family: 'Klein Text Trial', sans-serif;
    // font-weight: bold;
    font-size: calc(2rem + 0.5vw) !important;
    color: v.$blue;
}

p,
li {
    color: gray;
    // font-family: 'Open Sauce One', sans-serif;
    // font-family: 'Open Sauce Two', sans-serif;
    // font-family: 'Open Sauce Sans', sans-serif;
    font-family: 'Times New Roman', Times, serif;
    font-size: calc(1.25rem + 0.25vw) !important;
}

th {
    font-family: 'Open Sauce One', sans-serif;
    font-size: calc(1rem + 0.25vw) !important;
}

td {
    font-family: 'Times New Roman', Times, serif;
    font-size: calc(1.25rem + 0.25vw) !important;
    color: gray !important;
}

.red-rectangle {
    padding: 0.5rem;
    width: 100%;
    height: 1rem;
    background-color: red;
    margin-bottom: 5rem;
}

.blog-single .single-blog blockquote::before {
    background-color: red !important;
}

#table-scroll {
    // height: 500px;
    max-height: 500px;
    overflow: scroll;
    width: 95%;

    flex-shrink: 0;
    overflow: auto;
    position: relative;
    width: 95%;
    z-index: 1;
    margin: auto;
    // height: 500px;

    &>* {
        width: 83.33%;
        margin: auto;
    }

    table {
        margin-bottom: 5rem;
        margin-left: 0;
        width: 95%;
        border-collapse: separate;
        border-spacing: 0;
        margin: 0;

        tbody>tr>td:first-child {
            background-color: #f2f2f2;

            &:nth-child(3) {
                background-color: red;
            }
        }

        tbody>tr>td:nth-child(2) {
            background-color: #f2f2f2;
            position: sticky;
            left: 0;
        }

        tbody>tr:first-child {
            background-color: #f2f2f2;
            position: sticky;
            top: 0;
            z-index: 3;
        }

        tr {
            td {
                padding: 0.5rem 2rem;
                white-space: nowrap;
                width: 1rem !important;
            }
        }

    }

    h3 {
        text-align: center;
        margin-bottom: 5rem;
    }

    tr:nth-child(even) {
        background-color: #f2f2f2;
    }
}

.panel {
    width: 95%;
    margin: auto;
    padding: 2rem;
    padding-left: 0;
    color: gray;
    // font-family: 'Open Sauce One', sans-serif;
    // font-family: 'Open Sauce Two', sans-serif;
    // font-family: 'Open Sauce Sans', sans-serif;
    font-family: 'Times New Roman', Times, serif;
    font-size: calc(1.25rem + 0.25vw) !important;
    label {
        padding-right: 1rem;
    }
    input {
        border: 1px solid #f2f2f2;
        background-color: #f2f2f2;
        padding-left: 1rem;
    }
}

header {
    display: flex;
    justify-content: center;

    img {
        width: calc(5rem + 25vw);
        margin: auto;
        margin-top: 2rem;
        margin-bottom: 2rem;
    }
}

.blog {
    .row-article {

        .blog-post-titie {
            a {
                font-family: 'Open Sauce One', sans-serif;
                font-size: calc(1rem + 0.25vw) !important;
            }
        }

        .blog-post-action {
            color: v.$blue;
            font-weight: bold;
        }

        @include m.tablet {
            justify-content: center;
        }
    }
}

.column-news {
    display: flex;
    width: 100%;
    flex-direction: column;
    margin: auto;
    @include m.desktop {
        width: 83.33%;
        flex-direction: row;
        &>* {
            width: 49.5%;
        }
    }
    .news {
        h3 {
            text-align: center;
        }
        background-color: #f2f2f2;
        padding: 2rem;
        border-radius: 1rem;
        margin-top: 5rem;
        margin-bottom: 2.5rem;
        .new-item {
            margin: 2rem;
            display: flex;
            .new-img {
                width: 50%;
                height: 25vw;
                @include m.desktop {
                    height: 10vw;
                }
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            .new-info {
                width: 40%;
                padding-left: 2rem;
                a {
                    transition: all 0.5s;
                    &:hover {
                        color: v.$blue;
                        transition: all 0.5s;
                    }
                }
            }
        }
    }
}

.blog-single {

    blockquote {
        margin-bottom: 0 !important;
    }

    p {
        text-align: justify;
    }

    .column-blog {
        justify-content: center;

        .single-blog {
            padding: 0;

            p {
                span {
                    font-weight: bold;
                    color: v.$blueDark;
                }
            }
        }
    }

    .two-columns {
        display: flex;
        flex-direction: column;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;

            &>* {
                width: 48%;
            }
        }
    }
}

.faq-container {
    width: 90%;

    .accordion-item {
        border: none;
        margin-bottom: 2rem;
    }

    h3 {
        text-align: center;
        margin-bottom: 5rem;
    }

    .accordion-header button {
        font-family: 'Open Sauce One', sans-serif;
        font-size: calc(1.25rem + 0.25vw) !important;
        font-weight: bold;
    }

    .accordion-body {
        font-family: 'Times New Roman', Times, serif;
        font-size: calc(1.25rem + 0.25vw) !important;
        color: gray;
    }
}

.accordion-button:hover,
.accordion-button:focus,
.accordion-button:not(.collapsed) {
    background-color: transparent !important;
    color: inherit !important;
    box-shadow: none !important;
}